export const returnValidatePosition = (
  requirements: PositionRequirements[],
  documents: DocumentUser[]
) => {
  let status = false;
  for (let i in requirements) {
    // console.log("Docs: ", requirements[i]);
    // if (requirements[i].position_requirement.length === 0) {
    //   if (
    //     (requirements[i] !== null &&
    //       documents !== null &&
    //       validateDocCategory(requirements[i], documents) !== null &&
    //       validateDocCategory(requirements[i], documents) !== undefined) ||
    //     validateDocCategoryRequired(requirements[i], documents) !== null
    //   ) {
    //     status = false;
    //   }
    // } else {
    //   if (
    //     validateDocSubcategoryOptional(requirements[i], documents).length !==
    //       0 &&
    //     validateDocSubcategoryOptional(requirements[i], documents).length ===
    //       requirements[i].position_requirement.filter(
    //         (exc) => exc.required === false
    //       ).length
    //   ) {
    //     status = false;
    //   } else {
    //     if (validateDocSubcategory(requirements[i], documents).length !== 0) {
    //       status = false;
    //     }
    //   }
    // }
    if (requirements[i].position_requirement.length === 0) {
      if (
        //Check if there are optional documents
        documents.find(
          (itm) =>
            itm.category_id ===
              requirements[i].category.user_detail_category_id &&
            itm.sub_category_id ===
              requirements[i].subcategory.user_detail_subcategory_id &&
            (itm.validation_status === DocumentStatus.Approved ||
              itm.validation_status === DocumentStatus.Waiting) &&
            requirements[i].required === false
        ) !== undefined
      ) {
        // If found one valid
        status = true;
      } else {
        // All must be valid
        if (
          (requirements[i] !== null &&
            documents !== null &&
            validateDocCategory(requirements[i], documents) !== null &&
            validateDocCategory(requirements[i], documents) !== undefined) ||
          validateDocCategoryRequired(requirements[i], documents) !== null
        ) {
          status = false;
        }
      }
    } else {
      // when length > 0: chec optional
      if (
        validateDocSubcategoryOptional(requirements[i], documents).length ===
          0 ||
        validateDocSubcategoryOptional(requirements[i], documents).length !==
          requirements[i].position_requirement.filter(
            (exc) => exc.required === false
          ).length
      ) {
        status = true;
      } else {
        // Check other required documents
        if (validateDocSubcategory(requirements[i], documents).length !== 0) {
          status = false;
        }
      }
    }
  }

  return status;
};

export const returnValidatePositionOptional = (
  requirements: PositionRequirements[],
  documents: DocumentUser[]
) => {
  let status = true;
  for (let i in requirements) {
    if (requirements[i].position_requirement.length === 0) {
      if (
        (requirements[i] !== null &&
          documents !== null &&
          validateDocCategory(requirements[i], documents) !== null &&
          validateDocCategory(requirements[i], documents) !== undefined) ||
        validateDocCategoryRequired(requirements[i], documents) !== null
      ) {
        status = false;
      }
    } else {
      if (
        validateDocSubcategoryOptional(requirements[i], documents).length !==
          0 &&
        validateDocSubcategoryOptional(requirements[i], documents).length ===
          requirements[i].position_requirement.filter(
            (exc) => exc.required === false
          ).length
      ) {
        status = false;
      }
    }
  }
  return status;
};
