import React, { Component } from "react";
import { MdCheckCircle } from "react-icons/md";
import { Redirect } from "react-router";
import { Auth, candidateApplyStoreInterface } from "../../../../actions";
import { CandidateExams } from "../../../CandidateExams/CandidateExams";

interface CongratulationProps {
  auth: Auth;
  candidateApply: candidateApplyStoreInterface;
  cleanCandidateApply: () => void;
}
interface CongratulationState {
  redirect: boolean;
}

class Congratulation extends Component<
  CongratulationProps,
  CongratulationState
> {
  constructor(props: CongratulationProps) {
    super(props);

    this.state = {
      redirect: false,
    };
  }
  render() {
    if (this.state.redirect === true) {
      this.props.cleanCandidateApply();
      return <Redirect to="/applications" />;
    }
    return (
      <div className="bg-white mt-5 animate__animated animate__bounceIn">
        <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24 mt-5 bg-white py-5">
          <div className="text-center mt-3">
            <div className="w-full flex items-center justify-center">
              <MdCheckCircle className="text-7xl text-green-600 animate__animated animate__jackInTheBox animate-" />
            </div>
            <div className="font-extrabold text-4xl">Congratulations</div>
            <div className="mt-5 font-light text-lg">
              <span>Congratulations, Dear </span>
              <span className="font-semibold uppercase">
                {this.props.auth.user.fname} {this.props.auth.user.lname}{" "}
              </span>
              , you have completed your application for the{" "}
              <span className="font-bold">
                {this.props.candidateApply.selectedPosition?.position_title}
              </span>
              <br />
              <span className="ml-2">
                Note that, once you are shortlisted you will need to come back
                to this system to choose the Examination center.
              </span>
              .
            </div>
            {/* Exams list */}
            <div className="text-left bg-gray-100 px-2 pl-6 pt-1 mt-2 rounded-md border">
              <div className="text-xl font-base text-left -ml-2 mt-2 text-blue-600 flex w-full items-center justify-between gap-2">
                <div>Examination timetable</div>
                <div className="underline cursor-pointer text-base font-bold">
                  {/* Download my timetable */}
                </div>
              </div>
              {/* Table here */}
              <CandidateExams asComponent={true} />
            </div>
            <div className="flex w-full items-center justify-end py-3 mt-5 gap-3">
              <div
                onClick={() => {
                  this.props.cleanCandidateApply();
                }}
                className="bg-gray-100 hover:bg-gray-200 text-gray-800 btn cursor-pointer font-light"
              >
                Continue to apply
              </div>
              <div
                onClick={() => {
                  this.setState({ redirect: true });
                }}
                className="bg-blue-100 hover:bg-blue-200 text-blue-600 btn cursor-pointer font-light border border-blue-500"
              >
                Go to applications
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Congratulation;
